import { format } from 'd3-format';
import { groupBy, mapValues, round, size, sumBy } from 'lodash';
import React, { memo, useMemo } from 'react'
import { VictoryBar, VictoryStack } from 'victory';

import ChartBase from './ChartBase';
import theme from './theme';

const StackedBar = memo(({ data, total, keys, filtered, yMax, valueFormat, ...props }) => {
  const keysColor = useMemo(() => keys.reduce((kc, k, i) => {
    kc[k] = theme.group.colorScale[i]
    return kc
  }, {}), [keys])
  const totalLabels = useMemo(() => total?.values.map(t => valueFormat ? valueFormat(t.y) : t.y), [total, valueFormat])
  const precision = useMemo(() => size(totalLabels ? totalLabels[0].toString().split('.')[1] : 0), [totalLabels])
  const sumLabels = useMemo(() => {
    const group = groupBy(data.reduce((all, dd) => [...all, ...dd.values], []), 'x')
    return Object.values(mapValues(group, g => valueFormat ? valueFormat(sumBy(g, 'y')) : round(sumBy(g, 'y'), precision)))
  }, [data, precision, valueFormat])
  const labels = filtered ? sumLabels : total.values.map(t => t.y)
  return (
    <ChartBase
      domain={{ y: [0, yMax] }}
      tooltipLabels={({ datum }) => datum.y ? `${datum.childName}\n${valueFormat ? valueFormat(datum.y) : datum.y}` : ''}
      tickFormat={valueFormat}
      yMax={yMax}
      keysColor={keysColor}
      {...props}
    >
      <VictoryStack labels={labels}>
        {data.map(({ key, values }, i) => {
          return (
            <VictoryBar
              key={key + i}
              name={key}
              data={values}
              style={{ data: { fill: keysColor[key] } }}
              barRatio={0.8}
            />
          );
        }).reverse()}
      </VictoryStack>
    </ChartBase>
  )
})

StackedBar.Percent = props => <StackedBar {...props} valueFormat={format('.0%')} />

export default StackedBar
