import React, { useMemo } from 'react'
import { VictoryAxis, VictoryChart, VictoryLabel, VictoryTooltip, VictoryVoronoiContainer } from 'victory';

import theme from './theme';

const ChartBase = ({
  children,
  xUnit,
  unit,
  xPadding,
  yMax,
  tooltipLabels,
  domain,
  noXUnit,
  padding = {},
  tickValues,
  tickFormat,
  keysColor,
  animate,
}) => {
  const yAxisWidth = useMemo(() => Math.max(3.5, yMax.toLocaleString().length), [yMax])
  return (
    <VictoryChart
      domainPadding={{ x: xPadding, y: 20 }}
      theme={theme}
      domain={domain}
      animate={animate && { duration: 500 }}
      padding={{ ...theme.chart.padding, ...padding, left: yAxisWidth * 13 }}
      containerComponent={tooltipLabels &&
        <VictoryVoronoiContainer
          // mouseFollowTooltips
          labels={tooltipLabels}
          labelComponent={<VictoryTooltip constrainToVisibleArea style={{ fill: ({ text }) => keysColor[text?.[0]] }} />}
          voronoiBlacklist={['ghost']}
        />
      }
    >
      <VictoryAxis
        style={{ grid: { stroke: 'none' }, axisLabel: { padding: 8 } }}
        label={!noXUnit && `(${xUnit})`}
        axisLabelComponent={<VictoryLabel dx={160}/>}
      />
      <VictoryAxis
        dependentAxis
        tickValues={tickValues}
        tickFormat={tickFormat}
        label={unit}
        style={{
          axis: { stroke: 'none' },
          axisLabel: {
            writingMode: 'vertical-rl',
            textOrientation: 'mixed',
            transform: 'rotate(180deg)',
          }
        }}
        axisLabelComponent={<VictoryLabel x={8} />}
      />
      {children}
    </VictoryChart>
  )
}

export default ChartBase
