import { range, round } from 'lodash';
import React, { memo, useCallback, useMemo } from 'react'
import { VictoryAxis, VictoryBar, VictoryGroup, VictoryLabel, VictoryLine } from 'victory';

import ChartBase from './ChartBase';
import theme from './theme';

const formatYmax = ymax => {
  if (ymax < 15) return 16
  return 16000
}

const BarAndLine = ({ data, data2, keys, radioToggle, unit2, yMax, barWidth, ...props }) => {
  // const precision = useMemo(() => data[0].values[0].y.toString().split('.')[1] ? 1 : 0 , [data])

  const keysColor = useMemo(() => keys.reduce((kc, k, i) => {
    kc[k] = theme.group.colorScale[i]
    return kc
  }, {}), [keys])
  const roundedYMax = useMemo(() => yMax.map(formatYmax), [yMax])
  const yAxisWidth = useMemo(() => roundedYMax[1].toLocaleString().length, [roundedYMax])
  const tickValues = range(0, 1.125, 0.125)
  const getTickFormat = useCallback(i => (t) => Math.round(t * roundedYMax[i]), [roundedYMax])
  return (
    <ChartBase
      // tooltipLabels={({ datum }) => `${datum.childName}\n${datum.y}`}
      yMax={yMax[0]}
      padding={{ right: yAxisWidth * 13 }}
      domain={{ y: [0, 1] }}
      tickValues={tickValues}
      tickFormat={getTickFormat(0)}
      {...props}
    >
      <VictoryAxis
        dependentAxis
        tickValues={tickValues}
        label={unit2}
        offsetX={-330}
        style={{
          axis: { stroke: 'none' },
          axisLabel: {
            writingMode: 'vertical-rl',
            textOrientation: 'mixed',
            transform: 'rotate(180deg)',
          }
        }}
        tickFormat={getTickFormat(1)}
        axisLabelComponent={<VictoryLabel dx={34} />}
      />
      <VictoryGroup offset={barWidth}>
        {data.map(({ key, values }, i) => {
          return (
            <VictoryBar
              key={key + i}
              name={key}
              barWidth={barWidth}
              data={values.map(v => ({ ...v, y: v.y / roundedYMax[0] }))}
              style={{ data: { fill: keysColor[key] } }}
              labels={({ datum }) => round(datum.y * roundedYMax[0], 1)}
            />
          );
        })}
      </VictoryGroup>
      {data2.map(({ key, values }) => (
        <VictoryLine
          key={key}
          name={key}
          data={values.map(v => ({ ...v, y: v.y / roundedYMax[1] }))}
          style={{ data: { stroke: keysColor[key] } }}
          labels={({ datum }) => round(datum.y * roundedYMax[1], 0)}
        />
      ))}
    </ChartBase>
  )
}

export default memo(BarAndLine)
