import { round } from 'lodash';
import React, { memo, useMemo } from 'react'
import { VictoryBar, VictoryGroup } from 'victory';
import ChartBase from './ChartBase';

import theme from './theme';

const Bar = ({ data, keys, radioToggle, barWidth, ...props }) => {
  const precision = useMemo(() => data[0].values[0].y.toString().split('.')[1] ? 1 : 0 , [data])

  const keysColor = useMemo(() => keys.reduce((kc, k, i) => {
    kc[k] = theme.group.colorScale[i]
    return kc
  }, {}), [keys])
  return (
    <ChartBase
      // tooltipLabels={({ datum }) => `${datum.childName}\n${datum.y}`}
      {...props}
    >
      <VictoryGroup offset={barWidth}>
        {data.map(({ key, values }, i) => {
          return (
            <VictoryBar
              key={key + i}
              name={key}
              data={values}
              barWidth={barWidth}
              style={{ data: { fill: radioToggle ? undefined : keysColor[key] } }}
              labels={({ datum }) => round(datum.y, precision)}
            />
          );
        })}
      </VictoryGroup>

    </ChartBase>
  )
}

export default memo(Bar)
